@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* TODO Add site wide styles */



/* Global CSS Variables */
:root {
    /* Variable Demo:
        background-color: var(--color-background);
    */
    /* #fefefe */
    --color-white: rgb(252, 248, 247);
    --color-light-gray: rgb(235, 235, 235);
    --color-gray: rgb(177, 177, 177);
    --color-dark-gray: rgb(90, 90, 90);
    --color-black: rgb(36, 36, 36);

    --color-purple: rgb(122, 123, 255);
    --color-light-apricot: rgb(255, 243, 239);
    --color-apricot: rgb(255, 201, 181);
    --color-red: rgb(255, 88, 88);
}

* {
    box-sizing: border-box;
}

body {
    max-width: 1280px;
    min-height: 75vh;
    /* min-height: 1200px; */
    margin: 72px auto 60px;
    color: rgb(215, 155, 134);
    color: rgb(199, 199, 250);
    font-family: 'Barlow', sans-serif;
    /* TODO Change later or as needed */
    background-color: var(--color-white);
    /* color: var(--color-text-lighter); */
    color: black;
}



* {
    box-sizing: border-box;
}


/* BASE ELEMENT STYLES */
ul {
    list-style: none;
    padding: 0;
}

a {
    text-decoration: none;
    color: white;
}

small {
    font-size: 10px;
}

h4 {
    font-weight: 500;
    padding: 0;
    margin: 0;
}

button {
    cursor: pointer;
}

/* REUSABLE CLASS RULES */
.red-outline {
    border: 1px solid red;
}


.blur {
    filter: blur(4px);
}


.hidden {
    opacity: 0;
    display: none;
}

.line-clamp {
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}

#post-upload {
    height: 760px;
    width: 600px;
    margin: 100px auto;
}

#post-form {
    height: 100%;
    width: 100%;
    padding: 16px 0;

    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

/* IMAGE PREVIEW ************************/
#upload-and-preview-section {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#preview {
    width: 500px;
    height: 500px;

    object-fit: cover;

    border: 1px solid var(--color-gray);
}
#preview img {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    margin: auto;
}

#upload {
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    position: absolute;
    bottom: 24px; left: 0; right: 0;

}

#select-file-button {
    height: 26px;
    width: 160px;
    margin: 0 auto;
    padding: 12px;
    border: none;
    border-radius: 8px;

    font-size: 12px;
    background-color: var(--color-purple);
    cursor: pointer;

    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}


/* CAPTION/UPLOAD SECTION */
.upload__bottom {
    height: 100%;

    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
#caption {
    width: 500px;
    padding: 10px;
    border-radius: 4px;
    resize: none;

    outline: none;
}
#caption:focus {
    border-radius: 4px;
    border: 1px solid var(--color-purple)
}


#upload-button {
    height: 32px;
    width: 250px;
    margin: 0 auto;
    padding: 12px;
    border: none;
    border-radius: 8px;

    font-family: inherit;
    font-size: 14px;
    background-color: var(--color-purple);
    cursor: pointer;

    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

#cancel-button {
    height: 32px;
    width: 250px;
    margin: 0 auto;
    padding: 12px;
    border: none;
    border-radius: 8px;

    font-family: inherit;
    font-size: 14px;
    border: 1px solid var(--color-purple);
    background-color: transparent;
    cursor: pointer;

    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}



/* MISC */
.character-count {
    position: relative;
    top: -10px;
}

.error-container {
    padding: 16px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.error-container > div::before {
    content: '* ';
    color: red;
}

.upload-button-container {
    display: flex;
    flex-direction: column;
    grid-gap: 6px;
    gap: 6px
}

#empty-feed {
    width: 100%;
    position: relative;
}

#empty-feed img {
    width: 33.333%;
    filter: grayscale(0.2);
    z-index: -2;
}


.empty-feed-header {
    padding: 24px;

    position: absolute;
    z-index: 4;
    top: 50px; left: 0; right: 0;
    margin: 0 auto;
    background-color: rgba(252, 248, 247, 0.712);
    transition: opacity 1s;
}
.empty-feed-header * {
    color: white;

}


#all-posts {
    width: 100%;
    max-width: 1280px;
    display: flex;
    flex-flow: row nowrap;
    margin: 100px auto;
}

.posts-list {
    display: flex;
    width: 33.33%;
    flex-flow: column nowrap;
    overflow: hidden;
}


.no-feed-posts {
    font-family: inherit;
    padding-top: 15px;
    margin: auto;
    font-weight: 600;
    /* font-style: italic; */
    font-size: larger;
    text-align: center;
    color: rgba(0, 0, 0, 0.736);
    letter-spacing: 1px;
}

#modal {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

#modal_background {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.635);
}

#modal_content {
	height: 640px;
	width: 1000px;
	color: black;
	border-radius: 6px;
	overflow: hidden;

	background-color: var(--color-white);
    box-shadow: rgba(49, 49, 49, 0.411) 0px 10px 40px 20px;

	position: relative;
}

#post-image {
	cursor: pointer;
	box-shadow: none;
	transition: box-shadow .15s ease-in-out, transform .2s ease-in-out;
}

#post-image:hover {
	transform: scale(1.004);
	box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.profile-icon-border {
    width: 100%;
    height: 100%;
    padding: 1px;
    border-radius: 100%;
    border: 1px solid var(--color-purple);
    overflow: hidden;
    cursor: pointer;
}

.profile-icon {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 100%;
}

.comment-container {
    width: 100%;
    min-height: 50px;
    display: flex;
    flex-flow: row nowrap;
}
/* PROF IMG */
.comment-profile-image {
    width: 30px;
    height: 30px;
    border-radius: 20px;
}


.comment-body {
    width: 100%;
    padding: 0 10px;
    
    font-size: 13px;
    
    display: flex;
    flex-direction: column;
    position: relative;
}


.handle-and-comment-content {
    display: flex;
    flex-flow: column nowrap;
}
.comment-user-handle {
    font-weight: 500;
}
.comment-text {
    padding: 0 0 10px;
}


.edit-and-delete-buttons {
    font-size: 12px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    grid-gap: 8px;
    gap: 8px;
}

.edit-comment-form {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

#edit-comment-input {
    width: 100%;
    padding: 4px;
    border-radius: 2px;

    font-family: inherit;
    background-color: transparent;
    outline: none;
    resize: vertical;
}
#edit-comment-input:focus {
    border-color: var(--color-purple);
}
#edit-comment-input::-webkit-scrollbar {
	width: 8px;
}
#edit-comment-input::-webkit-scrollbar-thumb {
	border-radius: 25px;
    cursor: pointer;
	background-color: var(--color-gray);

}
#edit-comment-input::-webkit-scrollbar-track {
	margin: 2px;
}


#edit-comment-button-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 6px;
}

#submit-edit-comment,
#cancel-edit-comment {
    border: none;
    background: none;
    cursor: pointer;
}

/* CREATE COMMENT *****************************
**********************************************/
#comment-form-wrapper,
#comment-form-wrapper form {
    width: 100%;
    height: 100%;
    position: relative;
}

#comment-input {
    height: 100%;
    width: 85%;
    padding: 8px;
    border: none;
    background-color: transparent;
    
    outline: none;
}
#comment-input::-webkit-input-placeholder {
    color: var(--color-gray);
}

#comment-submit {
    background-color: transparent;
    border: none;
    letter-spacing: 1px;
    font-size: 14px;
    
    cursor: pointer;
    color: var(--color-purple);
}


#comment-form-wrapper small {
    position: absolute;
    right: 21px;
    top: -16px;
    /* bottom: 18px; */
}

#caption-input {
    width: 100%;
    max-height: 120px;
    padding: 4px 6px;
    margin: 4px 0;
    border: 1px solid var(--color-gray);
    border-radius: 2px;
    
    font-family: inherit;
    outline: none;
    
    resize: vertical;
    background-color: transparent;
}
#caption-input:focus {
    border-color: var(--color-purple)
}
#caption-input:focus {
    border-color: var(--color-purple);
}
#caption-input::-webkit-scrollbar {
	width: 8px;
}
#caption-input::-webkit-scrollbar-thumb {
	border-radius: 25px;
    cursor: pointer;
	background-color: var(--color-gray);

}
#caption-input::-webkit-scrollbar-track {
	margin: 2px;
}




#cancel-caption-button, #save-caption-button {
    font-size: 14px;
    border: none;
    border-radius: 4px;
    width: 30%;
    height: 30px;
    margin: 5px 0 0 10px;
}

#edit-caption-lower {
    width: 100%;
    display: flex;
    justify-content: space-between;
}
#edit-post-button-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 6px;
    gap: 6px;
    
}
#edit-caption-lower button {
    margin: 0; border: 0;
    width: 60px;
    height: 24px;
    font-size: 12px;
    font-family: inherit;
    font-weight: 400;
}

#cancel-caption-button:hover, #save-caption-button:hover {
    cursor: pointer;
}

#save-caption-button {
    background-color: var(--color-purple);
    color: white;
}

.post-modal-wrapper {
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-columns: 640px 360px;
    grid-template-areas:
        'left right';
}

.post-image-wrapper {
    grid-area: left;
    max-height: 640px;
    background-color: rgb(29, 29, 29);
    display: flex;
    justify-content: center;
    align-items: center;
}

.post-image-wrapper img {
    height: 100%;
    max-height: 100%;
    max-width: 100%;

    object-fit: contain;
    /* display: block; */
}


/* RIGHT SIDE **********************************/
.post-modal__right {
    grid-area: right;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

#post-info {
    max-height: 200px;
    display: flex;
    flex-direction: column;
}

/* POST HEADER */
.post-header {
    min-height: 80px;
    width: 100%;
    padding: 16px;
    border-bottom: 1px solid var(--color-gray);

    display: flex;
    align-items: flex-start;
}

.post-details-container {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.post-details {
    /* max-width: 220px; */
    width: 100%;
    padding: 0 12px;
    overflow: hidden;
    word-wrap: break-word;
}

.post-buttons {
    padding: 4px;
    cursor: pointer;
    display: flex;
    grid-gap: 12px;
    gap: 12px;
}

.post-user-handle {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.post-caption {
    font-size: 14px;
}



/* COMMENT SECTION */
.comment-section {
    padding: 16px;
    height: 0px;
    border-bottom: 1px solid var(--color-gray);
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
}

.comment-section::-webkit-scrollbar {
    width: 0px;
}

.comment-section::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0);
}



/* LOWER RIGHT SIDE */
#likes-div {
    height: 100px;
    padding: 12px;
    border-bottom: 1px solid var(--color-gray);

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

#likes-div-icons {
    display: flex;
    grid-gap: 8px;
    gap: 8px;
}

#post-like-count {
    margin: 4px 0 8px;
}
#date-posted {
    /* margin: 8px 0; */
}

#like-button {
    color: var(--color-red);
    transition: color .15s ease-in-out;
}


#like-button:hover {
    color: rgb(206, 54, 54);
}

#create-comment {
    height: 48px;
    width: 100%;

    display: flex;
    align-items: center;
}

.single-feed-post {
    padding: 4px 12px;
    justify-content: center;
    overflow: hidden;
}

.post-image-div img {
    max-width: 100%;
    height: auto;
}

.post-info {
    height: 56px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.user-handle {
    text-decoration: none;
    color: #818080;
    padding: 0 12px;
}

.user-image-and-handle {
    display: flex;
    align-items: center;
    justify-content: center;
}


/* LIKE AND COMMENT BUTTONS */
.post-like-and-comment-count {
    width: 110px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}


.like-icon {
    color: var(--color-red);
    font-size: 20px;
    padding: 0 6px;

    cursor: pointer;

    position: relative;
    top: -1px;
    transform: scale(1.1);
    transition: color .2s ease-in-out;
}

.like-icon:hover {
    color: rgb(206, 54, 54)
}

.comment-icon {
    color: var(--color-gray);
    font-size: 22px;
    padding: 0 6px;
    cursor: pointer;

    position: relative;
    top: -1px;
}

.post-like, .post-comment {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    height: 20px;
}



/* PROFILE POST CARD *************************************
**********************************************************/
.post-div {
    width: 32%;
    transition: opacity .15s linear;
    position: relative;
}


.overlay {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    aspect-ratio: 1 / 1;

    color: var(--color-white);

    position: absolute;
    /* bottom: calc(100%-20px); */
    opacity: 0;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: opacity .15s linear;
}
.overlay:hover {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.516);
}
#blur-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    filter: blur(10px);
    background-color: rgba(0, 0, 0, 0.447);
}
#profile-post-image {
    z-index: -1;
}


.overlay__button-container {
    height: 20px;
    width: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    position: absolute;
    z-index: 50;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.centering-container {
    display: flex;
    justify-content: center;
    align-items: center;
}


.profile__post__icon {
    color:rgb(229, 155, 128);
    padding: 10px;
    font-size: 25px;
}


.like-button {
    background-color: transparent;
    border: none;
}

.like-container .like-icon {
    transform: scale(1.4);
}

#loading-spinner-container {
    position: fixed;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    display: flex;
    justify-content: center;
}

#loading-spinner {
    width: 120px;
}

#loading-spinner-container h4 {
    width: 400px;
    text-align: center;
    position: fixed;
    letter-spacing: 1px;
    top: 115px;
    left: 50%;
    transform: translate(-50%, -50%);
    

}

.search {
  height: 100%;
  position: relative;
  
  display: flex;
  align-items: center;
}

.search__form {
  width: 260px;
  height: 30px;
  padding: 0 10px;
  
  border: 1px solid var(--color-light-gray);
  background-color: white;
  border-radius: 4px;

  display: flex;
  align-items: center;
}
.search input {
  width: 100%;
  height: 50%;
  
  border: none;

  color: black;
  outline: none;
}


/* search menu pop up thing ***********************
**************************************************/
.search-filter {
  width: 300px;
  max-width: 300px;
  max-height: 280px;
  margin: 0;
  padding: 0;
  border: 2px solid var(--color-light-gray);
  border-radius: 4px;
  
  font-size: 14px;
  color: var(--color-dark-gray);
  
  background-color: var(--color-white);
  overflow-x: hidden;
  overflow-y: scroll;
  
  position: absolute;
  top: 70px;
  top: 50px; left: 0; right: 0;
  margin: 0 auto;
  z-index: 10;
  display: flex;
  flex-direction: column;
}
.search-filter::-webkit-scrollbar {
  width: 6px;
}
.search-filter::-webkit-scrollbar-thumb {
  background-color: var(--color-gray);
  border-radius: 25px;
}

span.search-item,
#search-message {
  width: 100%;
  min-height: 52px;
  padding: 10px 6px;
  border-bottom: 1px solid var(--color-light-gray);
  
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  background: none;
  cursor: pointer;
  
  display: flex;
  align-items: center;
  
  transition: background .15s;
}
.search-item:hover {
  background: rgb(179, 179, 249);
}
#search-message {
  color: var(--color-gray);
  min-height: 40px;
}

.search-item .item-details {
  padding: 0 10px;
  width: 95%;
  
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.item-details span {
  font-size: 16px;
  width: 100%;
  
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.item-details small {
  font-size: 12px;
  color: var(--color-gray)
}

#navbar {
	width: 100%;
	height: 64px;

	background-color: rgb(253, 223, 211);
	/* border-bottom: 1px solid rgb(255, 242, 237); */
	border-bottom: 1px solid rgb(251, 227, 217);

	position: fixed;
	top: 0;
	left: 0;
	z-index: 10;
	box-shadow: rgba(0, 0, 0, 0.035) 0px 5px 5px;
}
.navbar-container {
	width: 100%;
	max-width: 1280px;
	height: 100%;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;

}

#nav-background {
	height: 66px;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 110;
	background-color: rgba(0, 0, 0, 0.5);
	filter: blur(4px);
}


/* NAV LEFT */
.nav__left {
	width: 340px;

	display: flex;
	justify-content: space-between;
	align-items: center;
}

.logo-wrapper {
	height: 100%;
	display: flex;
	align-items: center;
}
.nav__logo {
	width: 40px;
}
h1 {
	margin: 0 12px;
	text-shadow: 5px 5px rgba(0, 0, 0, 0.205);
	letter-spacing: 2px;
	font-weight: 200;
	font-size: 24px;
}


/* NAV RIGHT */
.nav__right {
	width: 340px;
	height: 100%;

	display: flex;
	justify-content: space-between;
	align-items: center;
}


.nav__buttons {
	width: 136px;
	font-size: 18px;



	display: flex;
	justify-content: space-around;
}

.nav__buttons * { cursor: pointer }


.nav__stats {
	width: 120px;

	display: flex;
	justify-content: space-around;
}
.nav__stats > * {
	display: flex;
	flex-direction: column;
	align-items: center;
}


.nav__icon {
	/* color: #777777; */
	/* color: rgb(189, 189, 248); */
	color: #848484;
	transition: color .15s linear;
}

.nav__icon:hover {
	color: var(--color-purple)
}



.stack {
	display: flex;
	flex-direction: column;
	align-items: center;
}

/* FOLLOWER/FOLLOWING DROPDOWNS */

.sessionUser-followers, .sessionUser-followings {
	position: relative;
}

.sessionUser-followers button, .sessionUser-followings button {
	padding: 0;
}

.user-profile-avatar, .justify-left {
	width: inherit;
}


.follows-dropdown {
	position: absolute;
	top: 50px;
	right: 0;
	width: 175px;
	border-top: none;

	background-color: rgb(253, 223, 211);
	background-color: var(--color-white);
	box-shadow: 0px 10px 15px 2px rgba(0, 0, 0, 0.201);
	z-index: 5;

	text-align: right;

	max-height: 200px;
    overflow-y: auto;
    overflow-x: hidden;
}




.follows-dropdown::-webkit-scrollbar {
	width: 8px;
}


.follows-dropdown::-webkit-scrollbar-thumb {
	border-radius: 25px;
	background-color: rgb(237, 237, 237);

}

.follows-dropdown::-webkit-scrollbar-track {
