    background: linear-gradient(135deg, rgba(122,123,255,1) 12%, rgba(255,201,181,1) 91%);
    color: var(--color-white);
    transition: background .15s ease-in-out, box-shadow .15s ease-in-out;
}

#update-button:hover {
    background: linear-gradient(135deg, rgb(113, 113, 249) 18%, rgb(254, 192, 170) 91%);
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    cursor: pointer;
}

#updating h2 {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    display: flex;
    justify-content: center;
}

